html{
  background-color: #EDEDED;
}

body {
  background-color: #EDEDED;
  color: hsl(30, 10%, 4%);
  margin: 0;
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.custom-container{
  background-color: #EDEDED;
}

.custom-link{
  font-family: 'Karla', sans-serif;
  font-size:20px;
}

.d-card{
  margin: 20px;
}

.d-card-img {
  border-radius: 10px;
  width: 400px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.d-card-info {
  margin-top:10px;
  min-height: 100px;
}

.d-card-title {
  font-size:24px;
  margin: 0px;
}

.d-card-subtitle {
  font-size:16px;
  margin:0px;
}
.success-msg{
  color: #57A773;
  padding:10px 15px;
}

.error-msg{
  color: #EE6532;
  padding:10px 15px;
}

.portrait-bio{
  width:300px;
  font-size:12px;
  text-align: center;
}

.subject-list{
  font-family: 'Karla', sans-serif;
  width: 65%;
  font-size: 24px;
  margin: auto;
  list-style-type: none;
}

.hero-title{
  font-family: 'Stint Ultra Expanded', cursive;
  font-size: 70px;
  margin-bottom:10px;
}

.subtitle{
  font-family: 'Karla', sans-serif;
  font-size: 34px;
}

.hero-text {
  font-family: 'Assistant', sans-serif;
  font-weight: 300;
  font-size: 24px;
}

.footer-name {
  font-family: 'Stint Ultra Expanded', cursive;
  font-size: 24px;
}

.secondHero {
  margin: 0px auto;
}

.second-hero-title{
  font-family: 'Stint Ultra Expanded', cursive;
  font-size: 50px;
}

.second-hero-subtitle {
  font-family: 'Karla', sans-serif;
  font-size: 30px;
}

div.tiger-background {
  width:100%;
  height:200px;
  background: url('./assets/tigerbanner2000x200_2.png') center center no-repeat;
  background-size: cover;
}

div.hands-background {
  width:100%;
  height:200px;
  background: url('./assets/handsbanner2000x200.png') center center no-repeat;
  background-size: cover;
}

div.sun-background {
  width:100%;
  height:200px;
  background: url('./assets/sunsbanner2000x200.png') center center no-repeat;
  background-size: cover;
}

div.image-container div.align {
  position:relative;
  width:2000px;
  margin: 0;
  text-align: center;
}

.subject{
  font-family: 'Stint Ultra Expanded', cursive;
  font-size:28px;
  margin-right:25px;
}


.social-card{
  margin: 20px;
  border:transparent;
}

.social-card img {
  border: transparent;
  width: 50px;
  height: 50px;
}

a, a:visited 
{
    text-decoration: none;  
}

.education-title {
  font-family: 'Karla', sans-serif;
  font-size: 30px;
  margin-bottom:0px;
}
